<script>

import { mapFields } from 'vee-validate';
import { Validator } from 'vee-validate';
import Api from './urlConfig.js';

// import Login from './components/Login.vue'

import defaultAvatar from './assets/avatar.svg';

export default {
  data () {
    return {
      showLogin: false,
      avatar: defaultAvatar,
      nickname: ''
    }
  },

  methods: {
    switchLogin(){
      this.showLogin = !this.showLogin;
    },
    sendAble(){
      return this.remailFlags.valid && this.rpasswordFlags.valid;
    },
    regAble(){
      return this.sendAble() && this.rcheckcodeFlags.valid;
    },
    logAble(){
      return this.emailFlags.valid && this.passwordFlags.valid;
    },
    sendEmail(){
      if(this.isSendEmail){
        this.showPopup('');
        return ;
      }
      if(!this.sendAble()){return ;}

      this.sendReq('I0051', {
        EmailAddress: this.regEmail
      })
      .then((res) => {
        if(res.data.StatusCode == 0){
          this.isSendEmail = true;
          this.showPopup(res.data.Info);
        }
      })
      .catch((err) => { console.log(err); });
    },
    regAccount(){
      if(!this.regAble()){
        alert('');
        return ;
      }

      this.sendReq('I0052', {
        email: this.regEmail,
        password: this.regPWD,
        emailCheck: this.regCheckcode
      })
      .then((res) => {
        this.showPopup(res.data.Info);
        if(res.data.StatusCode == 0){
          var userInfo = res.data.data;
          this.setCookie(userInfo);
        }
      })
      .catch((err) => { console.log(err); });
    },
    logAccount(){
      // if(!this.logAble()){
      //   alert('');
      //   return ;
      // }

      this.$validator.validate('logEmail', this.logEmail).then((result) => {
        if(result){
          this.$validator.validate('logPWD', this.logPWD).then((info) => {
            if(!info){
              this.showPopup(this.errors.first('logPWD'));
            }
          })
        } else {
          this.showPopup(this.errors.first('logEmail'));
        }
      });

      this.sendReq('I0015', {
        loginType: 1,
        email: this.logEmail,
        password: this.logPWD
      })
      .then((res) => {
        console.log(res);
        this.showPopup(res.data.Info);
        if(res.data.StatusCode == 0){
          var userInfo = res.data.data;
          this.setCookie(userInfo);
          this.showLogin = false;
        }
      })
      .catch((err) => { console.log(err); });
    },
    showPopup(text){
      var _this = this;
      _this.popupVisible = true;
      _this.popUpText = text;
      setTimeout(function(){
        _this.popupVisible = false;
      }, 3000);
    },
    validateForm(scope) {
      this.$validator.validateAll(scope).then((result) => {
        console.log(result);
      });
    }
  },

  computed: {
    ...mapFields({
      emailFlags: 'logEmail',
      passwordFlags: 'logPWD',
      remailFlags: 'regEmail',
      rpasswordFlags: 'regPWD',
      rcheckcodeFlags: 'regCheckcode'
    })
  },

  // components: {
  //   Login
  // },

  created(){
    if(this.checkLogin()){
      var userInfo = this.getCookie();
      console.log(userInfo);
      this.avatar = Api.avatarUrl + userInfo[2];
      this.nickname = userInfo[3];
    }
  }
}
</script>

<style lang="scss">
