    }
    .user-form-tab {
      line-height: 30px;
      height: 30px;
      margin-bottom: 30px;
      a {
        opacity: 0.6;
        font-size: 20px;
        &.reg-btn {
          margin-right: 10px;
        }
        &.active {
          opacity: 1;
          font-size: 28px;
        }
      }
    }
    .form-area {
      position: relative;
      margin-bottom: 25px;
      .checkcode-area {
        width: 45%;
      }
      .checkcode-btn {
        position: absolute;
        top: 0;
        right: 0;
        width: 40%;
        height: 100%;
        font-size: 16px;
      }
    }
    .input-area {
      position: relative;
      padding-left: 48px;
      background-color: #fff;
      border-radius: 4px;
      .mint-cell {
        background-color: transparent;
      }
      .tips {
        position: absolute;
