      }
    }
    .submit-btn {
      height: 48px;
      margin-bottom: 10px;
    }
    .login-form-tips {
      text-align: center;
      opacity: 0.6;
    }
    .popup-tips {
      width: 100%;
      height: 50px;
      line-height: 50px;
      text-align: center;
      background-color: rgba(0,0,0,.7);
    }
  }
  .sociality-container {
    &.login {
      .sociality-text {
        &::before, &::after {
          width: 30%;
        }
      }
      .icon {
        background-color: #fff;
      }
    }
  }
</style>


