import Vue from 'vue'
import VeeValidate, {Validator} from 'vee-validate'
import zh from 'vee-validate/dist/locale/zh_CN';//

// 
Validator.addLocale(zh);

const config = {
  locale: 'zh_CN'
};

Vue.use(VeeValidate,config);

// validate 
const dictionary = {
   zh_CN: {
      messages: {
        email: () => '',
        required: ( field )=> "" + field
      },
      attributes:{
        regEmail: '',
        regPWD: '',
        logEmail: '',
        logPWD: '',
        regCheckcode: ''
      }
  }
};

Validator.updateDictionary(dictionary);

