<template>
  <div id="newsletter-container" class="animated fadeIn">
    <header class="zdcj-header">
      <mt-navbar class="zdcj-h-nav" v-model="selectedNav">
        <mt-tab-item id="newslive" href="#/newslive">7*24</mt-tab-item>
        <mt-tab-item id="headlines" href="#/headlines"></mt-tab-item>
      </mt-navbar>
      <aside class="zdcj-h-aside">
        
      </aside>
    </header>
    <div class="newsletter-container">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      selectedNav: 'headlines'
    }
  },

  methods: {

  },

  created(){
  }
}
</script>

<style lang="scss">
  @import "./style/mixin";
  .zdcj-header {
    position: fixed;
    top: 0;
    left: 0;
    @include nav-style;
    width: 100%;
    height: 55px;
    z-index: 9999;
    .zdcj-h-nav {
      width: 70%;
      margin-left: 10%;
      background-color: transparent;
      .mint-tab-item {
        &.is-selected {
          color: #fff;
          margin-bottom: 0;
        }
        .mint-tab-item-label {
          font-size: 18px;
        }
      }
    }
    .zdcj-h-aside {
      position: absolute;
      top: 0;
      right: 0;
      width: 20%;
      font-size: 14px;
      padding: 17px 0;
      text-align: center;
    }
  }
  .newsletter-container {
    margin-top: 55px;
  }
</style>


