import Vue from 'vue'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'

import './style/common.scss'

import VueRouter from 'vue-router'
import VueCookie from 'vue-cookie'
import Index from './Index.vue'
import Headlines from './Headlines.vue'
import NewsLive from './NewsLive.vue'
import Newsletter from './Newsletter.vue'
import NewsDetail from './NewsDetail.vue'
import UserCenter from './UserCenter.vue'
import LoginPopup from './components/LoginPopup.vue'
import Test from './test.vue'

Vue.use(MintUI)
Vue.use(VueRouter)
Vue.use(VueCookie)

const routes = [
  { path: '/', name: 'app', component: Index },
  { path: '/newsletter', name: 'shixun', component: Newsletter, 
  children: [
    {path: '/headlines', name: 'zdtt', component: Headlines},
    {path: '/newslive', name: 'kuaixun', component: NewsLive},
  ]},
  { path: '/news/:name', name: 'newsDetail', component: NewsDetail },
  { path: '/user', name: 'userPage', component: UserCenter },
  { path: '/login', name: 'loginPage', component: LoginPopup },
  { path: '/test', name: 'testPage', component: Test }
]

const router = new VueRouter({
  routes //  routes: routes,
})

import Filter from './utils/filters.js'
import Common from './utils/common.js'
import './utils/validate.js'

Vue.use(Common)

Object.keys(Filter).forEach(key => {
  Vue.filter(key, Filter[key])
})

new Vue({
  el: '#app',
  router,
  render: h => h(Index)
})



