<template>
  <div id="news-container" class="news-container">
      <h3 class="news-title">{{newsTitle}}</h3>
      </mt-cell>
      <mt-cell 
      class="news-options"
      :title="newsAuthor"
      :label="newsTime | dateFormat">
        <div class="switch-font">
          <i class="watch-icon"></i>
          Aa
        </div>
      </mt-cell>
      <!-- <div class="news-author">
        <mt-cell 
        :title="newsAuthor"
        label=" 1023 ">
          <mt-button size="small" type="danger">+</mt-button>
          <img slot="icon" src="./assets/logo.png" width="50" height="50">
        </mt-cell>
      </div> -->
      <div class="news-intro">
        <span></span>
        <p>{{newsIntro}}</p>
      </div>
      <div class="news-content" v-html="newsContent">
      </div>
      <div class="qrcodebox">
        <img src="./assets/qrcode.jpg" alt="">
      </div>
      <div class="sociality-container share">
        <p class="sociality-text"></p>
        <mt-tabbar>
          <mt-tab-item id="">
            <div class="weibo-icon icon" slot="icon">
              <img src="./assets/weibo.svg">
            </div>
            
          </mt-tab-item>
          <mt-tab-item id="">
            <div class="wechat-icon icon" slot="icon">
              <img src="./assets/wechat.svg">
            </div>
            
          </mt-tab-item>
          <mt-tab-item id="">
            <div class="qq-icon icon" slot="icon">
              <img src="./assets/qq.svg">
            </div>
            QQ
          </mt-tab-item>
        </mt-tabbar>
      </div>
      <div class="share-bar">
        <mt-tabbar fixed>
          <mt-tab-item>
            <img slot="icon" src="./assets/tab-comment.svg">
          </mt-tab-item>
          <mt-tab-item>
            <img slot="icon" src="./assets/tab-favorite.svg">
          </mt-tab-item>
          <mt-tab-item>
            <img slot="icon" src="./assets/tab-share.svg">
          </mt-tab-item>
          <mt-tab-item>
            <img slot="icon" src="./assets/tab-thumbs.svg">
          </mt-tab-item>
        </mt-tabbar>
      </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
        newsId: '',
        newsContent: '',
        newsTitle: '',
        newsIntro: '',
        newsAuthor: '',
        newsTime: new Date()
    }
  },

  methods: {
    getNewsInfo(){
      this.sendReq('I0004', {
        type: 0,
        key: this.newsId
      })
      .then((res) => {
        console.log(res.data.data);

        var reg = /\d+/;
        var newsDetail = res.data.data[0];
        this.newsContent = newsDetail.contents;
        this.newsTitle = newsDetail.title;
        this.newsIntro = newsDetail.shortInfo;
        this.newsAuthor = newsDetail.Author;
        newsDetail.time = parseInt(newsDetail.time.match(reg)[0]);
        this.newsTime = new Date(newsDetail.time);
      })
      .catch((err) => { console.log(err); });
    }
  },

  created(){
    this.newsId = this.$route.params.name;
    this.getNewsInfo();
  }
}
</script>

<style lang="scss">
  .news-container {
    padding: 20px 20px 0;
    margin-bottom: 60px;
  }
  .news-title {
    font-size: 20px;
    text-align: justify;
  }
  .news-options {
    .mint-cell-wrapper {
      padding: 0;
    }
    .mint-cell-text {
      font-size: 14px;
    }
    .mint-cell-label {
      display: inline-block;
      vertical-align: middle;
      font-size: 14px;
      margin-top: 0;
    }
    .switch-font {
      position: relative;
      padding-left: 24px;
      font-size: 18px;
      color: #333;
      .watch-icon {
        position: absolute;
        left: 0;
        top: 0;
        width: 18px;
        height: 18px;
        background-size: 100%;
        background-position: center;
        background-image: url(./assets/fire.svg);
      }
    }
  }
  .news-author {
    margin: 0 -20px 20px;
    background-color: #f8f8f8;
    .mint-cell-wrapper {
      padding: 10px 20px;
      img {
        width: 50px;
      }
    }
    .mint-cell-text {
      font-size: 18px;
      margin-left: 5px;
    }
    .mint-cell-label {
      display: inline-block;
      vertical-align: middle;
      margin-top: 0;
      font-size: 14px;
      margin-left: 5px;
    }
  }
  .news-intro {
    position: relative;
    margin-bottom: 20px;
    padding: 10px;
    background-color: #0068b7;
    color: #fff;
    line-height: 30px;
    text-align: justify;
    span {
      position: absolute;
      left: 10px;
      top: 10px;
    }
    p {
      text-indent: 4em;
    }
  }
  .news-content {
    p {
      line-height: 30px;
      margin-bottom: 10px;
      text-indent: 2em;
      text-align: justify;
    }
    img {
      display: block;
      margin: 0 auto;
      max-width: 100%;
      height: auto;
    }
  }
  .share-bar {
    .mint-tabbar {
      background: none;
      border-top: 1px solid #e8e8e8;
    }
    .mint-tab-item {
      padding: 13px 0;
    }
    .mint-tab-item-icon {
      margin: 0 auto;
    }
    .mint-tabbar > .mint-tab-item.is-selected {
      background-color: #fff;
    }
  }
  .qrcodebox {
    margin-bottom: 20px;
  }
  .sociality-container {
    &.share {
      .icon {
        border: 2px solid #f55656;
      }
      .weibo-icon {
        border: 2px solid #f55656;
      }
      .wechat-icon {
        border: 2px solid #8ee566;
      }
      .qq-icon {
        border: 2px solid #2cb1e5;
      }
    }
  }

</style>


